<template>
  <div>
    <ul>
      <!--这种是简单插槽-->
      <li>
        <slot name="easy_header"></slot>
        <slot name="easy_body"></slot>
      </li>

      <!--循环插槽-->
      <li v-for="(items,index) in list" :key="items.name">
        <slot :items="items">
          {{items.name}}
        </slot>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  props:{
    list:{
      type:Array,
      required:true,
      default:[]
    }
  },
