<template>
  <div>
    <h1>1、这种是简单插槽 </h1>
    <Sloted :list="list">
      <template slot="easy_header">
        <p>顶部内容</p>
      </template>
      <template slot="easy_body">
        <p>中间内容</p>
      </template>
      <template slot-scope="slotProps">
        <span>复杂</span>
        {{ slotProps.items.name }}
      </template>
    </Sloted>
  </div>
</template>
<script>
import Sloted from './../components/slot.vue'
export default {
  data(){
    return{
      list:[{
        name:'a'
      },{
        name:'b'
      },{
        name:'c'
      },{
        name:'d'
      }]
    }
  },
  created() {
    
  },
  mounted() {
    
  },
  components:{
    Sloted
  }
}
</script>
